import { useQuery } from '@tanstack/react-query';
import { DataUtil } from '@youfoundation/transit-lib';
import { useEffect, useState } from 'react';
import { createAuthenticationProvider } from '../../provider/AuthenticationProvider';

const SHARED_SECRET = 'HSS';

const useAuth = () => {
  const [authenticationState, setAuthenticationState] = useState<
    'unknown' | 'anonymous' | 'authenticated'
  >('unknown');

  const checkToken = () => {
    const fetchData = async () => {
      if (!getSharedSecret()) {
        return false;
      }

      const client = createAuthenticationProvider();
      return await client.hasValidToken();
    };
    return useQuery(['verifyToken'], fetchData, {
      refetchOnMount: false,
    });
  };

  const { data: hasValidToken, isFetchedAfterMount } = checkToken();

  const authenticate = (identity: string, returnUrl: string): void => {
    const strippedIdentity = identity.replace(new RegExp('^(http|https)://'), '').split('/')[0];

    const client = createAuthenticationProvider();
    client.authenticate(strippedIdentity, returnUrl);
  };

  const finalizeAuthentication = (
    rsaEncryptedSharedSecret64: string | null,
    returnUrl: string | null
  ) => {
    if (!rsaEncryptedSharedSecret64 || !returnUrl) {
      return;
    }

    //TODO RSA decrypt
    const ss = rsaEncryptedSharedSecret64;
    window.localStorage.setItem(SHARED_SECRET, ss);
    window.location.href = returnUrl;
  };

  const logout = async (): Promise<void> => {
    const client = createAuthenticationProvider();
    await client.logout();
    window.localStorage.removeItem(SHARED_SECRET);
    setAuthenticationState('anonymous');
  };

  const getSharedSecret = () => {
    const raw = window.localStorage.getItem(SHARED_SECRET);
    if (raw) {
      return DataUtil.base64ToUint8Array(raw);
    }
  };

  useEffect(() => {
    if (isFetchedAfterMount && hasValidToken !== undefined) {
      if (hasValidToken) {
        setAuthenticationState('authenticated');
      } else {
        setAuthenticationState('anonymous');
        window.localStorage.removeItem(SHARED_SECRET);
      }
    }
  }, [hasValidToken]);

  return {
    authenticate,
    finalizeAuthentication,
    logout,
    getSharedSecret,
    isAuthenticated: authenticationState === 'authenticated',
  };
};

export default useAuth;
