import { FormEventHandler } from 'react';
import { MouseEventHandler } from 'react';
import { useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import useAuth from '../../../hooks/auth/useAuth';
import Person from '../../Icons/Person/Person';

const LOCAL_STORAGE_PREV_IDENTITY_KEY = 'previousIdentity';

const ProfileNav = () => {
  const { authenticate, isAuthenticated, logout } = useAuth();

  const previousIdentity = localStorage.getItem(LOCAL_STORAGE_PREV_IDENTITY_KEY);

  const [isOpen, setIsOpen] = useState(false);
  const [identity, setIdentity] = useState(previousIdentity ?? '');

  const doLogin: FormEventHandler = (e) => {
    e.preventDefault();

    localStorage.setItem(LOCAL_STORAGE_PREV_IDENTITY_KEY, identity);
    authenticate(identity, window.location.href);
  };

  const doLogout: MouseEventHandler = async (e) => {
    e.preventDefault();

    await logout();
    // Not finalized yet, it's here to avoid adding global state management already.. (we will need it probably, but just not yet)
    window.location.reload();
  };

  const doRegister: MouseEventHandler = (e) => {
    e.preventDefault();

    console.log('register');
  };

  return (
    <div className="relative">
      <button
        className={`inline-flex h-8 w-8 flex-shrink-0 items-center justify-center    rounded-full ${
          isOpen ? 'bg-slate-300 dark:bg-slate-500 ' : 'bg-slate-100 dark:bg-slate-700'
        }`}
        onClick={() => setIsOpen(!isOpen)}
      >
        <Person className="h-4 w-4" />
      </button>
      {isOpen ? (
        <div className="absolute top-[100%] right-0 z-10 pt-2 ">
          <div className="min-w-[20rem] bg-slate-100 p-8 pt-6 shadow-md dark:bg-slate-700">
            {!isAuthenticated ? (
              <>
                <form onSubmit={doLogin}>
                  <p className="text-lg">YouAuth</p>
                  <label
                    htmlFor="youniverse-id"
                    className="text-sm leading-7 text-gray-600 dark:text-gray-400"
                  >
                    Youniverse Id
                  </label>
                  <input
                    type="text"
                    name="youniverse-id"
                    id="youniverse-id"
                    defaultValue={previousIdentity ?? ''}
                    required
                    className="w-full rounded border border-gray-300 bg-gray-100 bg-opacity-50 py-1 px-3 text-base leading-8 text-gray-700 outline-none transition-colors duration-200 ease-in-out focus:border-indigo-500 focus:bg-transparent focus:ring-2 focus:ring-indigo-200 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-100"
                    onChange={(e) => setIdentity(e.target.value)}
                  />
                  <button className="mt-2 block w-full rounded border-0 bg-green-500 py-2 px-4 text-white hover:bg-green-600 focus:outline-none ">
                    {t('login')}
                  </button>
                </form>
                <p className="my-3 text-center">or</p>
                <button
                  onClick={doRegister}
                  className="block w-full rounded border-0 bg-indigo-500 py-2 px-4 text-white hover:bg-indigo-600 focus:outline-none "
                >
                  {t('signup')}
                </button>
              </>
            ) : (
              <>
                <p className="mb-4">
                  {t('Logged in with: ')}{' '}
                  <a
                    href={previousIdentity ?? ''}
                    className="underline"
                    target={'_blank'}
                    rel="noopener noreferrer"
                  >
                    {previousIdentity}
                  </a>
                </p>
                <button
                  onClick={doLogout}
                  className="mt-2 block w-full rounded border-0 bg-green-500 py-2 px-4 text-white hover:bg-green-600 focus:outline-none "
                >
                  {t('logout')}
                </button>
              </>
            )}
          </div>
        </div>
      ) : (
        ''
      )}
    </div>
  );
};

export default ProfileNav;
