import { useQuery } from '@tanstack/react-query';
import {
  ApiType,
  AttributeDefinitions,
  BuiltInProfiles,
  PublicClient,
  HomePageAttributes,
  Attribute,
} from '@youfoundation/transit-lib';
import useAuth from '../auth/useAuth';

type SiteData = {
  owner: {
    firstName?: string;
    surName?: string;
    profileImageId?: string;
  };
  social: {
    twitter?: string;
    instagram?: string;
    facebook?: string;
    linkedin?: string;
  };
  home: {
    template?: string;
    tagLine?: string;
    leadText?: string;
    headerImageFileId?: string;
  };
};

const useSiteData = () => {
  const { getSharedSecret } = useAuth();

  const fetchData: () => Promise<SiteData> = async () => {
    const client = new PublicClient({ api: ApiType.YouAuth, sharedSecret: getSharedSecret() });
    const fileData = await client.fileReadOnlyProvider.GetFile('profile.json');

    const getOwnerData = async () => {
      // File based response if available
      if (fileData.has('name') && fileData.has('photo')) {
        const nameAttr = fileData.get('name')?.[0]?.payload as Attribute;
        const photoAttr = fileData.get('photo')?.[0]?.payload as Attribute;

        if (nameAttr && photoAttr) {
          return {
            firstName: nameAttr?.data.givenName,
            surName: nameAttr?.data.surname,
            profileImageId: photoAttr?.data.profileImageId,
          };
        }
      }

      const [nameAttr, photoAttr] = await Promise.all([
        client.profileDataProvider.getBestAttributeVersion(
          BuiltInProfiles.StandardProfileId.toString(),
          BuiltInProfiles.PersonalInfoSectionId.toString(),
          AttributeDefinitions.Name.type.toString()
        ),
        client.profileDataProvider.getBestAttributeVersion(
          BuiltInProfiles.StandardProfileId.toString(),
          BuiltInProfiles.PersonalInfoSectionId.toString(),
          AttributeDefinitions.Photo.type.toString()
        ),
      ]);

      return {
        firstName: nameAttr?.data.givenName,
        surName: nameAttr?.data.surname,
        profileImageId: photoAttr?.data.profileImageId,
      };
    };

    const getSocialData = async () => {
      let fileBasedResponse: {
        twitter?: string;
        instagram?: string;
        facebook?: string;
        linkedin?: string;
        tiktok?: string;
      } = {};

      if (fileData.has('socials')) {
        fileData.get('socials')?.forEach((entry) => {
          fileBasedResponse = { ...fileBasedResponse, ...entry.payload?.data };
        });
      }

      if (Object.keys(fileBasedResponse).length !== 0) {
        return fileBasedResponse;
      }

      const socialAttributes = await Promise.all([
        await client.profileDataProvider.getBestAttributeVersion(
          BuiltInProfiles.StandardProfileId.toString(),
          BuiltInProfiles.SocialIdentitySectionId.toString(),
          AttributeDefinitions.TwitterUsername.type.toString()
        ),
        await client.profileDataProvider.getBestAttributeVersion(
          BuiltInProfiles.StandardProfileId.toString(),
          BuiltInProfiles.SocialIdentitySectionId.toString(),
          AttributeDefinitions.InstagramUsername.type.toString()
        ),
        await client.profileDataProvider.getBestAttributeVersion(
          BuiltInProfiles.StandardProfileId.toString(),
          BuiltInProfiles.SocialIdentitySectionId.toString(),
          AttributeDefinitions.FacebookUsername.type.toString()
        ),
        await client.profileDataProvider.getBestAttributeVersion(
          BuiltInProfiles.StandardProfileId.toString(),
          BuiltInProfiles.SocialIdentitySectionId.toString(),
          AttributeDefinitions.LinkedinUsername.type.toString()
        ),
        await client.profileDataProvider.getBestAttributeVersion(
          BuiltInProfiles.StandardProfileId.toString(),
          BuiltInProfiles.SocialIdentitySectionId.toString(),
          AttributeDefinitions.TiktokUsername.type.toString()
        ),
      ]);

      return {
        twitter: socialAttributes[0]?.data.twitter,
        instagram: socialAttributes[1]?.data.instagram,
        facebook: socialAttributes[2]?.data.facebook,
        linkedin: socialAttributes[3]?.data.linkedin,
        tiktok: socialAttributes[4]?.data.tiktok,
      };
    };

    const getHomeData = async () => {
      // File based response if available
      if (fileData.has('home') && fileData.has('theme')) {
        const homeAttribute = fileData.get('home')?.[0]?.payload as Attribute;
        const themeAttribute = fileData.get('theme')?.[0]?.payload as Attribute;

        if (homeAttribute && themeAttribute) {
          const tagLine = homeAttribute?.data.tagLine;
          const leadText = homeAttribute?.data.leadText;
          const headerImageFileId = homeAttribute?.data.headerImageUrl;

          const homePageTheme = themeAttribute?.data.themeId;

          return {
            template: homePageTheme,
            tagLine: tagLine,
            leadText: leadText,
            headerImageFileId: headerImageFileId,
          };
        }
      }

      // Full response
      const [homeAttribute, themeAttribute] = await Promise.all([
        client.homePageReadOnlyProvider.getBestAttributeVersion(
          HomePageAttributes.HomePage.toString()
        ),
        client.homePageReadOnlyProvider.getBestAttributeVersion(
          HomePageAttributes.Theme.toString()
        ),
      ]);
      // Page Config
      const tagLine = homeAttribute?.data.tagLine;
      const leadText = homeAttribute?.data.leadText;
      const headerImageFileId = homeAttribute?.data.headerImageUrl;

      const homePageTheme = themeAttribute?.data.themeId;

      return {
        template: homePageTheme,
        tagLine: tagLine,
        leadText: leadText,
        headerImageFileId: headerImageFileId,
      };
    };

    const newData = {
      owner: await getOwnerData(),
      social: await getSocialData(),
      home: await getHomeData(),
    };

    return newData;
  };

  return useQuery(['siteData'], fetchData, {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
    staleTime: Infinity,
    onError: (er) => {
      console.error(er);
    },
  });
};

export default useSiteData;
