import Facebook from '../../Icons/Facebook/Facebook';
import Twitter from '../../Icons/Twitter/Twitter';
import Instagram from '../../Icons/Instagram/Instagram';
import Linkedin from '../../Icons/Linkedin/Linkedin';
import Tiktok from '../../Icons/Tiktok/Tiktok';

const Socials = ({
  socialHandles,
  className,
}: {
  socialHandles?: {
    tiktok?: string;
    twitter?: string;
    facebook?: string;
    instagram?: string;
    linkedin?: string;
  };
  className: string;
}) => {
  return (
    <span className={`inline-flex ${className}`}>
      {socialHandles?.tiktok ? (
        <a
          className="ml-3 text-gray-500"
          href={`https://tiktok.com/${socialHandles.tiktok}`}
          target="_blank"
          rel="nofollow noopener"
        >
          <Tiktok className="h-5 w-5" />
        </a>
      ) : null}
      {socialHandles?.instagram ? (
        <a
          className="ml-3 text-gray-500"
          href={`https://instagram.com/${socialHandles.instagram}`}
          target="_blank"
          rel="nofollow noopener"
        >
          <Instagram className="h-5 w-5" />
        </a>
      ) : null}
      {socialHandles?.facebook ? (
        <a
          className="ml-3 text-gray-500"
          href={`https://facebook.com/${socialHandles.facebook}`}
          target="_blank"
          rel="nofollow noopener"
        >
          <Facebook className="h-5 w-5" />
        </a>
      ) : null}
      {socialHandles?.twitter ? (
        <a
          className="ml-3 text-gray-500"
          href={`https://twitter.com/${socialHandles.twitter}`}
          target="_blank"
          rel="nofollow noopener"
        >
          <Twitter className="h-5 w-5" />
        </a>
      ) : null}

      {socialHandles?.linkedin ? (
        <a
          className="ml-3 text-gray-500"
          href={`https://linkedin.com/${socialHandles.linkedin}`}
          target="_blank"
          rel="nofollow noopener"
        >
          <Linkedin className="h-5 w-5" />
        </a>
      ) : null}
    </span>
  );
};

export default Socials;
